import * as React from 'react'
import Web3Modal from 'web3modal'
import WalletConnectProvider from '@walletconnect/web3-provider'
import { providers } from 'ethers'

export const useWalletconnect = () => {
  const [account, setAccount] = React.useState(null)

  const [web3Modal, setWeb3Modal] = React.useState(null)

  React.useEffect(() => {
    const providerOptions = {
      walletconnect: {
        package: WalletConnectProvider,
        options: {
          appName: 'Loteria de Babilonia',
          alchemyId: 'Pv7z_NKo_kmwxcuS8i2shIT3IwUEJbd1',
        },
      },
    }

    const newWeb3Modal = new Web3Modal({
      cacheProvider: false, // very important
      disableInjectedProvider: false,
      network: 'goerli',
      providerOptions,
    })

    setWeb3Modal(newWeb3Modal)
  }, [])

  async function connectWallet() {
    const web3ModalInstance = await web3Modal.connect()
    const web3ModalProvider = new providers.Web3Provider(web3ModalInstance)

    if (web3ModalProvider) {
      console.log(web3ModalProvider)
      const userAddress = await web3ModalProvider.getSigner().getAddress()
      console.log({ userAddress })
      setAccount(userAddress)
    }
  }

  return { account, connectWallet }
}
